import axios from 'axios';
import { showToast, showLoadingToast, showSuccessToast, closeToast, showConfirmDialog } from 'vant';
const projectSettings = require('@/settings.js');

// axios
const instance = axios.create({
  baseURL: process.env.VUE_APP_BASE_API + projectSettings.apiPath,
  timeout: 30000, // 
});

// request
// Add a request interceptor
instance.interceptors.request.use(
  config => {
    showLoadingToast({
      message: '...',
      duration: 0,
      forbidClick: true, //
    });
    // Do something before request is sent
    return config;
  },
  error => {
    // Do something with request error
    console.log(error); // for debug
    return Promise.reject(error);
  },
);

// Add a response interceptor
instance.interceptors.response.use(
  response => {
    // loading
    closeToast();
    // Any status code that lie within the range of 2xx cause this function to trigger
    // Do something with response data
    const res = response.data;
    // res.code http status code  res.sysCode  0
    if (res.code === 200 && res.sysCode === 0) {
      if (res.msg) {
        // apimsg
        showSuccessToast({
          message: res.msg,
          duration: 3 * 1000,
        });
      }
      return response.data;
    } else {
      // 200 errorMsg
      if (res.errorMsg) {
        showToast({
          message: res.errorMsg,
          duration: 5 * 1000,
        });
      }
      return Promise.reject(res);
    }
  },
  error => {
    // loading
    closeToast();
    // Any status codes that falls outside the range of 2xx cause this function to trigger
    // Do something with response error
    showConfirmDialog({
      title: '',
      message: '' + (error.message.indexOf('timeout') > -1 ? '' : ''),
      confirmButtonText: '',
    });
    return Promise.reject(error);
  },
);

export default instance;
